import axios from 'axios'

function axiosApi (method, url, params, success, failure, error) {
  axios({
    method: method,
    url: url,
    data: method === 'POST' || method === 'PUT' ? params : null,
    params: method === 'GET' || method === 'DELETE' ? params : null,
    baseURL: 'http://localhost/',
    withCredentials: true
  }).then(function (res) {
    if (res.data.success === true) {
      if (success) {
        success(res.data)
      }
    } else {
      if (failure) {
        failure(res.data)
      }
    }
  }).catch(function (err) {
    if (error) {
      error(err)
    }
  })
}

export default {
  get: function (url, params, success, failure, error) {
    return axiosApi('GET', url, params, success, failure, error)
  },
  post: function (url, params, success, failure, error) {
    return axiosApi('POST', url, params, success, failure, error)
  },
  put: function (url, params, success, failure, error) {
    return axiosApi('PUT', url, params, success, failure, error)
  },
  delete: function (url, params, success, failure, error) {
    return axiosApi('DELETE', url, params, success, failure, error)
  }
}



